@import url(https://fonts.googleapis.com/css2?family=Zen+Tokyo+Zoo&family=Tinosdisplay=swap);
@import url(https://fonts.googleapis.com/css2?family=Zen+Tokyo+Zoo&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  font-size: 16px;
  background-color: #aed6dc;
  color: #4a536b;
  font-family: 'Tinos', serif;
}



.choose-target-div {
    text-align: center;
    margin-top: 10%;
}

.targets {
    margin-bottom: .5rem;
}

.current-target {
    margin-bottom: 2rem;
    font-size: 130%;
}
.target-holder {
    width: 2.9rem;
    height: 2.9rem;
    border-radius: 50%;
    cursor: pointer;
    text-align: center;
    display: inline-block;
    margin: .6rem;
    opacity: .9;
    position: relative;
}

.target-holder:hover {
    opacity: 1;
}

.target {
    font-size: 126%;
    position: inherit;
    top: 23%;
    text-align: center;
}

.black {
    background-color: #21211d;
    color: #f5f17f;
    border: 2px solid #bd0f0f;
}

.yellow {
    background-color: #f5f17f;
    color: #21211d;
    border: 2px solid #0c7009;
}

.red {
    background-color: #bd0f0f;
    color: #0c7009;
}
.select-name {
    height: 2rem;
    text-align: center;
    font-size: 110%;
}
.action-button {
    margin: 1rem;
    width: 9rem;
    height: 3rem;
    font-size: 120%;
    letter-spacing: .1rem;
    background-color: #ff9a8d;
    border: 1px solid #4a536b;
    color: #4a536b;
}

.action-button:hover {
    background-color: #4a536b;
    color: #ff9a8d;
    border: 1px solid #ff9a8d;
}

.choose-target-button {
    width: 12rem;
    font-size: 100%;
}

.finish-button {
    width: 6.8rem;
    height: 2rem;
    font-size: 100%;
}
.game-details-div {
    text-align: center;
    margin-top: 5%;
}

.the-average-square {
    width: 3.6rem;
    height: 3.6rem;
    background-color: #4a536b;
    color: #ffffff;
    margin: 0 auto;
    margin-top: 2.5rem;
    border: 2px solid #000000;
    text-align: center;
    position: relative;
}

.the-average-span {
    font-weight: 400;
    font-size: 205%;
    position: inherit;
    top: 14%;
}

.finish {
    margin-top: 1.6rem;
    text-align: center;
}

.the-heighest-array {
    text-align: center;
    margin-top: 2rem;
}

.eachHeighestResult {
    margin: 1rem;
    padding: .1rem .49rem;
    background-color: #4a536b;
    font-size: 140%;
    font-weight: 700;
    color: #ffffff;
    border: 1px solid #000000;
}

.red {
    background-color: red;
}
.all-squares {
    text-align: center;
    margin-top: 5rem;
}
.the-square {
    width: 3rem;
    height: 3rem;
    border: 1px solid #000000;
    color: #4a536b;
    margin: 1rem;
    font-size: 129%;
    text-align: center;
}
.final-average {
    margin-bottom: 2rem;
    font-size: 105%;;
}

.stats-spans {
    margin-left: 1rem;
}
.game-finish-stats {
    margin-top: 4rem;
    text-align: center;
}
.home-page {
    margin-top: 15rem;
    text-align: center;
}

.home-title {
    font-size: 380%;
    font-family: 'Zen Tokyo Zoo', cursive !important;
}

.action-buttons {
    margin-top: 2rem;
}
